import './tweet.css'
import { Link } from 'react-router-dom'
export default function Tweet({ data }) {
  return (
    <div className="tweet">
      <div className="tweet-user">
        <div className="img icon">
          <img src={data.icon} alt="" />
        </div>
        <p><Link to={"/profile/"+data.userId}>{data.user}</Link><br /><span>{data.date}</span></p>
      </div>
      <p>{data.desc}</p>
      {data.media?
        data.media.type === 'image'?
          <div className="media">
            <div className="img">
              <img src={data.media.data} alt="" />
            </div>
            </div>
        :null
      :null}
      <div className="nums">
        <p>{data.comments} Comments</p>
        <p>{data.retweets} Retwwts</p>
        <p>{data.saves} Saved</p>
      </div>
      <div className="buttons">
        <button><i className="far fa-comment-alt"></i><span>Comments</span></button>
        <button><i className="fa fa-sync"></i><span>Retweet</span></button>
        <button><i className="far fa-heart"></i><span>Like</span></button>
        <button><i className="far fa-bookmark"></i><span>Saves</span></button>
      </div>
      <div className="reply">
        <div className="img icon">
          <img src={data.icon} alt="" />
        </div>
        <div className="input">
          <input placeholder="Tweet your reply" type="text" />
          <i className="fa fa-image"></i>
        </div>
      </div>
    </div>
  )
}