import './header.css'
import logo from '../../img/tweeter-small.svg'
import { Link } from 'react-router-dom'

export default function Header({actual, user, icon }) {
  return (
    <header>
      <div className="logo">
        <img src={logo} alt="logo" />
        <p>Tweeter</p>
      </div>
      <div className="tabs">
        <p className={actual==='Home'?"actual":null}><Link to="/">Home</Link></p>
        <p className={actual==='Explore'?"actual":null}><Link to="/Explore">Explore</Link></p>
        <p className={actual==='Bookmarks'?"actual":null}><Link to="/Bookmarks">Bookmarks</Link></p>
      </div>
      
      <div className="user">
        <div className="img">
          <img src={icon} alt="user" />
        </div>
        <p className="user-name">{user}</p>
        <input type="checkbox"  id="toggleMenu" />
        <label htmlFor="toggleMenu"><i className="fa fa-caret-down"></i></label>
        <div className="menu">
          <p><i className="fa fa-user"></i>My Profile</p>
          <p><i className="fa fa-user-friends"></i>Group Chat</p>
          <hr />
          <p id="signOut"><i className="fa fa-sign-out-alt"></i>Logout</p>
        </div>
        
      </div>
      
      <div className="tabs tabs2">
        <p className={actual==='Home'?"actual":null}><Link to="/"><i className="fa fa-home"></i></Link></p>
        <p className={actual==='Explore'?"actual":null}><Link to="/Explore"><i className="fas fa-compass"></i></Link></p>
        <p className={actual==='Bookmarks'?"actual":null}><Link to="/Bookmarks"><i className="fa fa-bookmark"></i></Link></p>
      </div>
    </header>
  )
}