import './filter.css'
export default function Filter({ tabs }) {
    return (
        <div className="filter">
            {tabs.map((tab, key) => {
                if(key !== 1)
                    return <p key={key}>{tab}</p>
                else
                    return <p className="actual" key={key}>{tab}</p>
            })}
        </div>
    )
}