import { useState } from "react"
import Header from "../components/header/header"
import Filter from "../components/filter/filter"
import Tweet from "../components/tweet/tweet"
import SearchBox from "../components/searchBox/searchBox"


var tweet = {
  icon: "https://thispersondoesnotexist.com/image",
  user: "Tweet User",
  date: "24 August at 20:43",
  desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
  media: false,
  comments: 400,
  retweets: 100,
  saves: 234,
  retweeted: true,
  liked: false,
  saved: false
}
var tweet2 = {
  icon: "https://thispersondoesnotexist.com/image",
  user: "Tweet User",
  date: "24 August at 20:43",
  desc: "2 Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
  media: {
    type: 'image',
    data: 'https://picsum.photos/700/350'
  },
  comments: 400,
  retweets: 100,
  saves: 234,
  retweeted: true,
  liked: false,
  saved: false
}

export default function Explore() {
  const [user, setUser] = useState({
    icon: "https://thispersondoesnotexist.com/image",
    name: "User Name"
  })

  return (
    <>
      <Header actual="Explore" icon={user.icon} user={user.name} />
      <br />
      <div className="contenedor">
        <Filter tabs={["Top","Lastest","People","Media"]} />
        <div>
          <SearchBox/>
          <div className="tweets">
              <Tweet data={tweet} />
              <Tweet data={tweet2} />
          </div>
        </div>
      </div>
    </>
  )
}