import './trends.css'
export default function Trends({trends}){
    function numReduce(num) {
        if (num > 999999)
            return Math.floor(num / 1000000) + "m"
        else if (num > 999)
            return Math.floor(num / 1000) + "k"
    }
    return(
        <div className="trends">
            <h1>Trends for you</h1>
            {trends.map((e, key)=>(
                <div key={key} className="trend">
                    <p>#{e.trend}</p>
                    <p>{numReduce(e.tweets)} Tweets</p>
                </div>
                
            ))}
        </div>
    )
}