import './usersList.css'
import { Link } from 'react-router-dom'

export default function UsersList({ user, users, title, toggle}) {
    return (
        <div className="users-list-container">
            <div className="users-list">
                <h1>{user} {title}<i onClick={toggle} className="fa fa-times"></i></h1>
                
                {users.map((e, key) => (
                    <div key={key} className="user-list">
                        <div className="data">
                            <div className="user-list-main">
                                <div className="img">
                                    <img src={e.icon} alt="" />
                                </div>
                                <h2><Link to={"/profile/"+e.id}>{e.name}</Link></h2>
                                <p>{e.followers} followers</p>
                            </div>
                            <p className="bio">{e.bio}</p>
                        </div>
                        {e.following?
                            <button>Following</button>
                        : 
                            <button><i className="fa fa-user-plus"></i> follow</button>
                        
                        }
                    </div>
                ))}
            </div>
        </div>
        
    )
}
