import { useState, useEffect } from 'react'
import { useParams } from 'react-router'
import Header from "../components/header/header"
import UserData from "../components/userData/userData"
import Filter from '../components/filter/filter'
import Tweet from '../components/tweet/tweet'
import UsersList from '../components/usersList/usersList'

export default function Profile() {
  const {id} = useParams()
  

  const [followersWindow,setFollowersWindow] = useState(false)
  const [followingWindow,setFollowingWindow] = useState(false)

  const [user, setUser] = useState({
    icon: "https://thispersondoesnotexist.com/image",
    name: "User Name"
  })

  const [other, setOther] = useState({
    icon: "https://thispersondoesnotexist.com/image",
    background: "https://picsum.photos/800/400",
    name: "User Name"
  })

  useEffect(() => {
    setFollowersWindow(false)
    setFollowingWindow(false)
    
  }, [id])
  var tweet = {
    icon: "https://thispersondoesnotexist.com/image",
    user: "Tweet User",
    date: "24 August at 20:43",
    desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
    media: false,
    comments: 400,
    retweets: 100,
    saves: 234,
    retweeted: true,
    liked: false,
    saved: false
  }
  var tweet2 = {
    icon: "https://thispersondoesnotexist.com/image",
    user: "Tweet User",
    date: "24 August at 20:43",
    desc: "2 Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
    media: {
      type: 'image',
      data: 'https://picsum.photos/700/350'
    },
    comments: 400, 
    retweets: 100,
    saves: 234,
    retweeted: true,
    liked: false,
    saved: false
  }

  const following = [
    {
      id:43,
      icon:"https://thispersondoesnotexist.com/image",
      name:"User Name 1",
      followers:300,
      bio:"1 Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
      following:false
    },
    {
      id:80,
      icon:"https://thispersondoesnotexist.com/image",
      name:"User Name 2",
      followers:3230,
      bio:"2 Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
      following:true
    },
    {
      id:31,
      icon:"https://thispersondoesnotexist.com/image",
      name:"User Name 3",
      followers:40,
      bio:"3 Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores non culpa veritatis quia.",
      following:false
    },
  ]



  const toggleFollowers = ()=>{
    setFollowersWindow(!followersWindow)
  }
  const toggleFollowing = ()=>{
    setFollowingWindow(!followingWindow)
  }
  

  return (
    <>
      <Header icon={user.icon} user={user.name} />
      <UserData
        showFollowing={toggleFollowing}
        showFollowers={toggleFollowers}
        background={other.background}
        icon={other.icon}
        user={other.name} />
      <div className="contenedor">
        <Filter tabs={["Tweets", "Tweets y replies", "Media", "Likes"]} />
        <div>
          <p style={{ color: "var(--gray3)", fontSize: 14 }} ><i className="fa fa-sync"></i> {user.name} Retweeted</p>
          <div className="tweets">
            <Tweet data={tweet} />
            <Tweet data={tweet2} />
          </div>
        </div>
      </div>
      {followingWindow?<UsersList toggle={toggleFollowing} title="is following" user={user.name} users={following}/>:null}
      {followersWindow?<UsersList toggle={toggleFollowers} title="followers" user={user.name} users={following}/>:null}
      

    </>
  )
}