import './toFollow.css'
import {Link} from 'react-router-dom'

function numReduce(num) {
    if (num > 999999)
        return Math.floor(num / 1000000) + "m"
    else if (num > 999)
        return Math.floor(num / 1000) + "k"
}

function FollowUser({userId, img, bio, user, followers, background }) {
    return (
        <div className="follow-user">
            <div className="data">
                <div className="img">
                    <img src={img} alt="user" />
                </div>
                <div>
                    <h2><Link to={"/profile/"+userId}>{user}</Link></h2>
                    <p>{numReduce(followers)} followers</p>
                </div>
                <button><i className="fa fa-user-plus"></i> Follow</button>
            </div>
            <p>{bio}</p>
            <div className="img">
                <img src={background} alt="" />
            </div>
        </div>

    )

}

export default function ToFollow({ users }) {
    return (
        <div className="to-follow">
            <h1>Who to follow</h1>
            {users.map((e, key) => (
                <FollowUser
                    key={key}
                    bio={e.bio}
                    img={e.img}
                    user={e.name}
                    followers={e.followers}
                    background={e.background}
                />
            ))}

        </div>
    )
}
