import './userData.css'
export default function UserData({background,icon,user,showFollowers,showFollowing}) {
  return (
    <div className="main-user">
      <div className="img">
        <img src={background} alt="back" />
      </div>
      <div className="user-data">
        <div className="img">
          <img src={icon} alt="user" />
        </div>
        <div className="user-text">
          <div className="user-text-head">
            <p id="bigUserName">{user}</p>
            <p onClick={showFollowers}>2,300 <span>Followers</span></p>
            <p onClick={showFollowing}>1,300 <span>Following</span></p>
          </div>

          <p className="user-bio">Lorem ipsum dolor sit amet consectetur adipisicing elit. Repudiandae magni laudantium nostrum delectus.</p>
        </div>

        <button><i className="fa fa-user-plus"></i> Follow</button>
      </div>
    </div>
  )
}