import './tweetForm.css'
import { useState } from "react"
export default function TweetForm({icon}){
    const [who,setWho] = useState({
        icon:'fa-globe-americas',
        text:'Everyone'
    })

    const changeReply = (who)=>{
        if(who === 1)
            setWho({
                icon:'fa-globe-americas',
                text:'Everyone'
            })
        else 
            setWho({
                icon:'fa-user-friends',
                text:'People you follow'
            })
    }
    
    return(
        <div className="tweet-form">
            <h1>Tweet something</h1>
            <div className="text">
                <div className="img">
                    <img src={icon} alt="icono" />
                </div>
                <textarea placeholder="What's happening?"></textarea>
            </div> 
            <div className="tweet-foot">
                <div className="settings">
                    <input style={{display:'none'}} type="checkbox" id="toggleWho" />
                    <i className="fa fa-image"></i>
        
                    <label className="toggle" htmlFor="toggleWho">
                        <i className={"fa "+who.icon}></i>
                        <span>{who.text} can reply</span>
                    </label>
                    <div className="who-settings">
                        <p>Who can reply?</p>
                        <p>Choose who can reply this Tweet</p>
                        <label htmlFor="toggleWho">
                            <p onClick={()=>{changeReply(1)}} className="item"><i className="fa fa-globe-americas"></i> Everyone</p>
                            <p onClick={()=>{changeReply(2)}} className="item"><i className="fa fa-user-friends"></i> People you follow</p>
                        </label>
                        
                    </div>
                </div>
                <button>Tweet</button>
            </div>
        </div>
    )
}